.hero{
    width: 100%;
    height: 100vh;
    position: relative;
}

.hero-mid{
    width: 100%;
    height: 70vh;
    position: relative;
}
.hero-mid h1{
    padding: 4rem !important;
}
.hero-mid-mid{
    width: 100%;
    height: 70vh;
    position: relative;
}
.hero-mid-mid-mid h1{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    font-size: 3.4rem;
    color: black;
    background-color:transparent;
    opacity:3;
}
img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.hero-text{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
}
.hero-text h1{
    font-size: 3rem;
    font-weight: 800;
    background: #fff;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    
}
.hero-text p{
    font-size: 1.5rem;
    
    color: #fff;
    padding: 0.5rem 0.2rem 0;
   
    margin-bottom: 30px;
}
.hero-text .show{
    text-decoration: none;
    background-color: white;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: black;
    

}
.hero-text .hide{
display: none;
}
@media source and (max-width: 555px)
{
    .hero-text h1{
        padding: 10px 20px;
    }
    .hero-text p{
        font-size: 1.1rem;
        padding: 0 0 2rem 0;
    }
    .hero-text .show{
        
        padding: 6rem 1.1rem;
        border-radius: 6px;
        font-size: 1.1rem;
    }
}